input[type="file"] {
	width: 350px;
	max-width: 100%;
	color: #444;
	padding: 5px;
	background: #fff;
	border-radius: 10px;
	border: 1px solid #555;

	&::file-selector-button {
		margin-right: 20px;
		border: none;
		background: #084cdf;
		padding: 10px 20px;
		border-radius: 10px;
		color: #fff;
		cursor: pointer;
		transition: background 0.2s ease-in-out;
	}

	&::file-selector-button:hover {
		background: #0d45a5;
	}
}

.drop-container {
	position: relative;
	display: flex;
	gap: 10px;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	height: 200px;
	padding: 20px;
	border-radius: 10px;
	border: 2px dashed #555;
	color: #444;
	cursor: pointer;
	transition: background 0.2s ease-in-out, border 0.2s ease-in-out;

	&:hover {
		background: #eee;
		border-color: #111;

		&.drop-title {
			color: #222;
		}
	}

	.drop-title {
		color: #444;
		font-size: 20px;
		font-weight: bold;
		text-align: center;
		transition: color 0.2s ease-in-out;
	}

	&.drag-active {
		background: #eee;
		border-color: #111;
	}

	& > .drag-active .drop-title {
		color: #222;
	}
}
